// 1.code -> data
// 2.code -> vuex -> 

export default {
    data: () => ({
        code: '',
        ctrl: null,
    }),
    computed: {},
    created() {
        this.getNewCode();
        this.setCron();
    },
    onShow() {
        this.getNewCode();
        this.setCron();
    },
    onHide() {
        clearTimeout(this.ctrl);
    },
    // 
    onUnload() {
        clearTimeout(this.ctrl);
    },
    methods: {
        getNewCode() {
            uni.login({
                success: (e) => {
                    this.code = e.code;
                },
            });
        },
        setCron() {
            clearTimeout(this.ctrl);
            // code
            this.ctrl = setTimeout(() => {
                this.getNewCode();
                // croncode
                this.setCron();
            }, 4 * 60 * 1000);
        },
    },
};
